<template>
   <v-app>
      <v-alert v-if="checkError" v-model="alert.Alert" :type="alert.typeAlert" transition="fade-transition">
        {{this.alert.message}}
      </v-alert>
       <v-snackbar
				v-model="snackbar"
				:color="NotifColor"
				:timeout="timeout"
				top
			>
					<p style="margin-bottom:0px;text-align:center;width: 100%;">{{NotifText}}</p>
			</v-snackbar>
      <v-layout align-center justify-center column>
         <div class="bxLogin">
            <div class="bxLogo">
               <img src="https://liveb2itest.id.dyned.com/assets/images/logo.png" height="35px;" alt="">
            </div>
            <div class="Bxtitle">
               Sign in
            </div>
            <div class="bxInput">
               <v-form ref="formlogin" v-model="valid" lazy-validation>
                  <v-text-field
                     v-model="email"
                     :rules="emailRules"
                     label="E-mail"
                     required
                     class="initest"
                     ></v-text-field>
                  <v-text-field
      							v-model="password"
      							:rules="[rules.required, rules.min]"
      							:type="show1 ? 'text' : 'password'"
      							name="input-10-1"
      							label="Password"
      							hint="At least 8 characters"
      							@click:append="show1 = !show1"
                            @keyup.enter="login"
            		  ></v-text-field>
                  <v-checkbox
                     v-model="checkbox"
                     label="Remember me"
                     required
                  ></v-checkbox>
                  <v-layout class="BxFbtn">
                     <p class="v-label fpassword" :disabled="dialog" @click="dialog = true">Forgot Your password?</p>
                     <v-btn :disabled="!valid" class="btnRound liveBtn text-capitalize" @click="login">
                        login
                     </v-btn>
                  </v-layout>
               </v-form>
            </div>
            <v-layout class="bxUs" align-center justify-space-between row>
               <div>
                  <span>About us</span>
               </div>
               <div>
                  <span>Contact us</span>
               </div>
            </v-layout>
         </div>
         <div class="ICHelp">
            <ICHelp width="28" height="28"/>
         </div>
      </v-layout>
      <v-dialog v-model="dialog" hide-overlay width="100" persistent>
         <v-card class="fpassword--loder">
            <v-card-text class="text-xs-center">
               <v-progress-circular
                  indeterminate
                  color="primary"
                  :size="50"
                  :width="7"
               ></v-progress-circular>
            </v-card-text>
         </v-card>
      </v-dialog>
      <v-dialog v-model="dialog2" width="400" content-class="DGfpassword" persistent>
         <v-card style="border-radius: 3px;">
            <v-card-title class="headline bxhead">
               <v-btn outline color="red" @click.native="dialog2 = false"><v-icon>close</v-icon></v-btn>
               <p>Forgot Password</p>
            </v-card-title>
            <v-card-text>
               <div class="bx__forgotpassword">
                  <v-form ref="formchange" v-model="valid2" lazy-validation>
                     <p style="font-size:0.9em;text-align:center;margin-bottom: auto;">We just need your registered email address to send you password reset.</p>
                     <br>
                     <v-text-field
                        v-model="emailConfirm"
                        :rules="emailRules"
                        label="Your E-mail"
                        required
                        class="initest"
                     ></v-text-field>
                     <br>
                     <v-card-actions style="justify-content:center;">
                        <v-btn color="primary" @click="changepassword" :disabled="!valid2" class="text-capitalize">
                           Submit
                        </v-btn>
                     </v-card-actions>
                  </v-form>
               </div>
            </v-card-text>
        </v-card>
      </v-dialog>
       <v-dialog v-model="dialogLoding" width="150" persistent content-class="PersonalLoading">
         <v-card>
            <v-card-text class="text-xs-center">
               <v-progress-circular
                  indeterminate
                  color="primary"
                  :size="70"
                  :width="7"
               ></v-progress-circular>
            </v-card-text>
         </v-card>
      </v-dialog>
   </v-app>
</template>

<script>
   import { mapState } from 'vuex';
   import ICHelp from '../../components/icons/RoundHelp.vue'

   export default{
      components:{
        ICHelp
      },
      data () {
  			return {
          valid: false,
          valid2: false,
          email: '',
          emailConfirm: '',
  				emailRules: [
  					v => !!v || 'E-mail is required',
  					v => /.+@.+/.test(v) || 'E-mail must be valid'
  				],
  				checkbox: false,
          show1: false,
          password: '',
          alert:{
            Alert: true,
            typeAlert: 'error',
            message: 'The email or password you entered don\'t match',
          },
  				rules: {
  					required: value => !!value || 'Required.',
  					min: v => v.length >= 8 || 'Min 8 characters',
            emailMatch: () => ('The email or password you entered don\'t match'),
            passMatch: (v) => v == this.newpassword || 'New Password and Confirm Password must match'
          },
          dialog: false,
          dialog2: false,
          snackbar: false,
          timeout: 6000,
          NotifColor:'',
          NotifText:'',
          dialogLoding: false,
  			}
      },
      mounted() {
          this.$store.dispatch("CLEAR_STATE");
      },
      watch: {
        dialog (val) {
          if (!val) return
          setTimeout(() => (this.dialog2 = true, this.dialog = false), 1000)
        },
        CheckForgotPS (val) {
           if (!val) return
           if(val.data.status === '200 OK'){
               this.NotifColor ='success'
               this.NotifText = 'We send the password reset link to your Email'
               this.dialogLoding = true
               setTimeout(()=>{
                  this.dialogLoding = false
                  this.snackbar = true
                  this.$refs.formchange.reset()
                  this.dialog2 = false
               },2000);
           }else if(val.data === 400){
               this.NotifColor ='error'
               this.NotifText = "CAN'T FIND YOUR ACCOUNT"
               this.dialogLoding = true
               setTimeout(()=>{
                  this.dialogLoding = false
                  this.snackbar = true
               },2000);
           }else if(val.data.email[0] === "validation.email"){
               this.NotifColor ='error'
               this.NotifText = 'EMAIL FORMAT IS WRONG'
               this.dialogLoding = true
               setTimeout(()=>{
                  this.dialogLoding = false
                  this.snackbar = true
               },2000);
           }
         }
      },
      computed:{
        checkError () {
          return this.showAlert(), this.$store.state.Error401
        },
        CheckForgotPS(){
           return this.$store.state.ForgotPass
        }
      },
      created() {
         console.log(this.CheckForgotPS)
      },
      methods:{
        login () {
            if (this.$refs.formlogin.validate()) {
               const formLogin ={
                  username: this.email,
                  password: this.password,
                  // returnSecureToken : true
               }
               const ConvertFormLogin = JSON.stringify(formLogin)
               this.$store.dispatch('POST_LOGIN', ConvertFormLogin)
            }
         },
        changepassword () {
          if (this.$refs.formchange.validate()) {
            const DataForgot = {
               email : this.emailConfirm
            }
            this.$store.dispatch('POST_FORGOT_PASSWORD', DataForgot)
          }
        },
        showAlert () {
          if (this.$store.state.Error401){
            this.valid=false
            setTimeout(() => (this.$store.state.Error401 = false, this.valid=true), 4000)
          }
        }
      }
   }
</script>
<style lang="scss" scoped>
    @import './Login.scss';
</style>



